<template>
<div>
   <h2>TodoList</h2>
   <div class="container">
    <label for="input-text">ToDoList</label>
    <div class="center_container">
      <Picker @changeDate="transmit"></Picker>
      <input
        type="text"
        v-model="inputValue"
        @keydown.enter="save"
        id="input-text"
        placeholder="添加ToDo"
      />
    </div>
  </div>
</div>
 
</template>
<script>
import Picker from "./Picker.vue";
export default {
  data() {
    return {
      inputValue: "",
    };
  },
  components: {
    Picker,
  },
  props: [],
  emits: ["save", "changeDate"],
  methods: {
    save() {
      if (this.inputValue.trim()) {
        this.$emit("save", this.inputValue);
        this.inputValue = "";
      }
    },
    transmit(value) {
      this.$emit("changeDate", value);
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  color: white;
  background: rgba(47, 47, 47, 0.98);
}
@media screen and (max-width: 800px) {
  h2{
    background-color:rgba(47, 47, 47, 0.98);
    color:white;
    text-align:center;
  }
  .center_container {
    display:flex;
    width: 100%;
    justify-content:space-between;
    align-items:center;
  }
  label {
    display: none;
  }
  input {
    margin-left:5px;
    height:40px;
    padding-left: 5px;
    border-radius: 5px;
    outline:none;
    border:none;
  }
}
@media screen and (min-width: 800px) {
  h2{
    display:none;
  }
  .center_container {
    display: flex;
    width: 600px;
    justify-content: space-between;
    align-items: center;
  }
  label {
    line-height: 48px;
    position: absolute;
    left: 20px;
  }
  input {
    width: 300px;
    height: 40px;
    padding-left: 5px;
    border-radius: 5px;
  }
}
</style>