<template>
  <li :style="styleObject">
    <input type="checkbox"  @change="move" :checked="checkedOrNot" />
    <p @click="isEdit = true" v-show="!isEdit" :style="sText">
      {{ modelValue }}
    </p>
    <input
      :value="modelValue"
      @input="$emit('update:modelValue', $event.target.value)"
      type="text"
      v-show="isEdit"
      @blur="blurDel"
    />
    <el-button
      @click="delFromList"
      class="del"
      type="primary"
      icon="el-icon-delete"
      size="small"
    ></el-button>
  </li>
</template>
<script>
export default {
  data() {
    return {
      isEdit: false,
    };
  },
  props: ["isTodo", "index", "modelValue"],
  emits: [
    "update:modelValue",
    "movetodo",
    "movetodone",
    "delFromTodo",
    "delFromDone",
  ],
  methods: {
    move() {
      if (this.isTodo == true) {
        this.$emit("movetodone", this.index);
      } else {
        this.$emit("movetodo", this.index);
      }
    },
    delFromList() {
      if (this.isTodo == true) {
        this.$emit("delFromTodo", this.index);
      } else {
        this.$emit("delFromDone", this.index);
      }
    },
    blurDel(){
      this.isEdit = false
      if(!this.modelValue.trim()){
        this.delFromList()
      }
    }
  },
  computed: {
    checkedOrNot() {
      return this.isTodo ? false : true;
    },
    styleObject() {
      if (this.isTodo) {
        return {
          "border-left": "5px solid red",
        };
      }
      return {
        "border-left": "5px solid green",
      };
    },
    sText() {
      if (!this.isTodo) {
        return {
          "text-decoration": "line-through",
          "color":"gray"
        };
      }
      return {
        "text-decoration": "none",
      };
    },
  },
};
</script>
<style scoped>
li {
  margin-bottom: 8px;
  height: 36px;
  display: flex;
  align-items: center;
  background-color: white;
}
li:last-of-type {
  margin-bottom: 0px;
}
input {
  zoom: 150%;
  margin-left: 5px;
}
p {
  margin-left: 5px;
  overflow: hidden;
}
.del {
  margin-left: auto;
  margin-right: 2px;
}
</style>