<template>
      <el-date-picker
        v-model="value"
        type="date"
        placeholder="Pick a day"
        :shortcuts="shortcuts"
        @change="$emit('changeDate',value)"
      >
      </el-date-picker>
</template>


 
<script>
export default {
  emits: ['changeDate'],
  props: [],
  data() {
    return {
      shortcuts: [
        {
          text: "Today",
          value: new Date(),
        },
        {
          text: "Yesterday",
          value: () => {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            return date;
          },
        },
        {
          text: "A week ago",
          value: () => {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            return date;
          },
        },
      ],
      value: new Date(),
    };
  },
  methods: {
    disabledDate(time) {
      return time.getTime() > Date.now();
    },
  },
};
</script>
