<template>
  <Header
    style="position: sticky; top: 0px; width:100%;"
    @save="saveToList"
    @changeDate="updateTodoList"
  ></Header>
  <div class="content">
    <div class="progress">
      <h2 style="margin-bottom: 5px">完成进度</h2>
      <el-progress type="circle" :percentage="percentage" />
    </div>
    <div class="center_content">
      <h2 id="todo-info" class="clearfix">
        <span id="todo-label">正在进行</span>
        <span id="todo-count">{{ doList["todolist"].length }}</span>
      </h2>
      <ol>
        <TodoItem
          v-for="(todo, index) in doList['todolist']"
          v-model="todo.text"
          :key="todo.id"
          :isTodo="true"
          :index="index"
          @movetodone="movetodone"
          @delFromTodo="delFromTodo"
        ></TodoItem>
      </ol>
      <h2 id="todo-info" class="clearfix">
        <span id="todo-label">已完成</span>
        <span id="todo-count">{{ doList["donelist"].length }}</span>
      </h2>
      <ol>
        <TodoItem
          v-for="(done, index) in doList['donelist']"
          v-model="done.text"
          :key="done.id"
          :isTodo="false"
          :index="index"
          @movetodo="movetodo"
          @delFromDone="delFromDone"
        ></TodoItem>
      </ol>
      <footer>
        <el-button type="text" @click="dialogVisible = true">Clear</el-button>
        <el-dialog v-model="dialogVisible" title="提示" width="30%">
          <span>清空本日TODO?</span>
          <template #footer>
            <span class="dialog-footer">
              <el-button @click="dialogVisible = false">取消</el-button>
              <el-button type="primary" @click="clearList">确认</el-button>
            </span>
          </template>
        </el-dialog>
      </footer>
    </div>
  </div>
  <el-backtop :bottom="80" :right="80"> UP </el-backtop>
</template>
<script>
import Header from "./Header.vue";
import TodoItem from "./TodoItem.vue";
export default {
  data() {
    return {
      dialogVisible: false,
      date: this.getDateString(new Date()),
      doList: {
        todolist: [],
        donelist: [],
      },
    };
  },
  created() {
    if (localStorage.getItem(this.date)) {
      this.doList = JSON.parse(localStorage.getItem(this.date));
    }
  },
  methods: {
    //将时间转换成字符串
    getDateString(date) {
      let month = date.getMonth();
      month = month < 9 ? "0" + (month + 1) : month + 1;
      return "" + date.getFullYear() + month + date.getDate();
    },
    saveToList(value) {
      this.doList["todolist"].push({
        id: this.doList["todolist"].length,
        text: value,
      });
      //同步到localStorange
      localStorage.setItem(this.date, JSON.stringify(this.doList));
    },
    //更新当天的TodoList界面
    updateTodoList(value) {
      localStorage.setItem(this.date, JSON.stringify(this.doList));
      this.date = this.getDateString(value);
      if (!localStorage.getItem(this.date)) {
        this.doList = {
          todolist: [],
          donelist: [],
        };
      } else {
        this.doList = JSON.parse(localStorage.getItem(this.date));
      }
    },
    movetodone(index) {
      this.doList["donelist"].push(this.doList["todolist"][index]);
      this.doList["todolist"].splice(index, 1);
      localStorage.setItem(this.date, JSON.stringify(this.doList));
    },
    movetodo(index) {
      this.doList["todolist"].push(this.doList["donelist"][index]);
      this.doList["donelist"].splice(index, 1);
      localStorage.setItem(this.date, JSON.stringify(this.doList));
    },
    delFromTodo(index) {
      this.doList["todolist"].splice(index, 1);
      localStorage.setItem(this.date, JSON.stringify(this.doList));
    },
    delFromDone(index) {
      this.doList["donelist"].splice(index, 1);
      localStorage.setItem(this.date, JSON.stringify(this.doList));
    },
    clearList() {
      this.doList = {
        todolist: [],
        donelist: [],
      };
      localStorage.setItem(this.date, JSON.stringify(this.doList));
      this.dialogVisible = false;
    },
  },
  computed: {
    percentage() {
      let per =
        (this.doList["donelist"].length * 100) /
        (this.doList["todolist"].length + this.doList["donelist"].length);
      //保留两位小数
      return per >= 0 ? Math.round(per * 100) / 100 : 0;
    },
  },
  components: {
    Header,
    TodoItem,
  },
};
</script>
<style scoped>
#todo-label {
  float: left;
}
#todo-count {
  float: right;
}
footer {
  margin-top: 10px;
  text-align: center;
}
@media screen and (max-width: 800px) {
  .center{
    width:100%;
  }
  .center_content {
    width: 100%;
  }
  #todo-info {
    margin: 0 auto;
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .progress {
    text-align:center;
    position: relative;
    margin: 0 auto;
  }
}
@media screen and (min-width: 800px){
  .content {
  display: flex;
}
.center_content {
  margin: 0 auto;
  width: 600px;
}
#todo-info {
  margin: 0 auto;
  width: 600px;
  margin-top: 15px;
  margin-bottom: 15px;
}
.progress {
  position: fixed;
  top: 120px;
  right: 120px;
  text-align: center;
}
a {
  text-decoration: none;
}
}

</style>
